<template>
  <div>
    <div class="btndiv">
      <el-input class="inp" v-model="inputValue" placeholder="请输入内容" clearable></el-input>
      <el-button class="btn" icon="el-icon-search" @click="search" >搜索</el-button>
      <el-button class="btn" icon="el-icon-plus" @click="addVideo" >添加</el-button>
      <!-- <el-button class="btn" icon="el-icon-delete" @click="delVideo" >删除</el-button> -->
      <el-button class="btn" icon="el-icon-refresh-left" @click="shuaXin" ></el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableList"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="60"> </el-table-column>
      <el-table-column label="ID" width="100">
        <template slot-scope="scope">{{ scope.row.id }}</template>
      </el-table-column>
      <el-table-column prop="name" label="接口名" width="230">
         <template slot-scope="scope">{{ scope.row.api_name }}</template> 
      </el-table-column>
      <el-table-column prop="url" label="接口地址" >
        <template slot-scope="scope">{{ scope.row.api_url }}</template> 
      </el-table-column>
      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>

     <el-dialog title="编辑接口" :visible.sync="dialogOpen">
          <div>
            <el-form :model="editTable" label-width="70px" ref="editUser">
              <el-form-item label="接口名称">
                <el-input v-model="editTable.api_name"></el-input>
              </el-form-item>
              <el-form-item label="url地址">
                <el-input v-model="editTable.api_url"></el-input>
              </el-form-item>
              
            </el-form>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogOpen = false">取 消</el-button>
            <el-button type="primary" @click="editTrue">确 定</el-button>
          </div>
        </el-dialog>
  </div>
</template>

<script>
import service from "@/utils/request";
export default {
  name: "jxList",
  data() {
    return {
      tableList:[],
      multipleSelection: [],
      editTable:{
        id:'',
        api_name:'',
        api_url:''
      },
      changeEdit:'',
      inputValue:'',
      dialogOpen:false,
      method:'',
    }
  },
  methods: {
    search(){
      this.getTabledata();
    },
    addVideo(){
      this.dialogOpen=true;
      this.method='add';
      this.editTable={
        id:'',
        api_name:'',
        api_url:''
      };
    },
    delVideo(){
      this.method='del';
      // 
    },
    shuaXin(){
      this.getTabledata();
    },
    editTrue(){
      // console.log('编辑确定');
      this.upt_add_Tabledata();
    },
    handleEdit(index,row){
      this.dialogOpen=true;
      this.method='upt';
      this.editTable=row;
    },
    handleDelete(index,row){
      // console.log('handleDelete',index,row);
      this.method='del';
      this.editTable=row;
       this.upt_add_Tabledata();
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log('val--',val);
    },
    getTabledata(){
      service
      .post("/admin/apilist", { 
          method: "get",
          search:this.inputValue,
        })
      .then((result) => {
        console.log("result",result.data[0]);
        this.tableList = result.data[0];
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('查询失败');
      });
    }, 
    upt_add_Tabledata(){
      let methodnow=this.method
      let editTable=this.editTable;
      console.log('methodnow:',methodnow,'editTable:',editTable);
      service
      .post("/admin/apilist", { 
          method: methodnow,
          edit:editTable,
        })
      .then((result) => {
          if(result.data[0].affectedRows==1){  //影响行数
            this.getTabledata();
            this.dialogOpen=false;
            this.$message({message: "操作成功！",type: "success",});
          }else{
            this.$message.error('操作失败');
          }
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('操作失败');
      });
    }, 
  },
  mounted() {
    this.getTabledata();
  },
};
</script>

<style scoped>
.btn{
  height: 40px;
  margin: 0 5px 0 0px;
  width: calc(7% - 4px);
  min-width: 86px;
  float: left;
}
.inp {
  float: left;
  margin-bottom: 5px;
  margin-right: 5px;
  width: calc(26% + 26px);
}
</style>